import { __ } from '@wordpress/i18n';
import { AlignmentToolbar, BlockControls, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, ToggleControl, __experimentalUnitControl as UnitControl, DateTimePicker, SelectControl, PanelRow, TabPanel, RangeControl } from '@wordpress/components';

// Variables
import Title from '../../Components/Title';
import Background from '../../Components/Background';
import Typography from '../../Components/Typography';
import BColor from '../../Components/BColor';
import SpaceControl from '../../Components/SpaceControl';
import BorderControl from '../../Components/BorderControl';
import ShadowControl from '../../Components/ShadowControl';
import options from './Const/options';
const { generalStyleTabs, layouts, flexPos, sepTypes, pxUnit, perUnit, emUnit, remUnit } = options;

const Settings = ({ attributes, setAttributes }) => {
    const { destDate, layout, boxIsInline, boxPosition, isLabels, isDays, isHours, isMinutes, isSeconds, width, background, padding, shadow, alignment, boxBG, boxWidth, boxHeight, boxSpace, boxBorder, boxShadow, digitTypo, digitColor, labelTypo, labelColor, isSep, sepType, sepSize, sepColor } = attributes;

    return <>
        <InspectorControls>
            <TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
                {'general' == tab.name && <>
                    <PanelBody className='bPlPanelBody' title={__('Countdown Settings', 'countdown-time')}>
                        <Title mt='0'>{__('Destination Date:', 'countdown-time')}</Title>
                        <DateTimePicker currentDate={destDate} onChange={val => setAttributes({ destDate: val })} is12Hour={true} />
                    </PanelBody>


                    <PanelBody className='bPlPanelBody' title={__('Layout Settings', 'countdown-time')} initialOpen={false}>
                        <PanelRow>
                            <Title mt='0' mb='0'>{__('Layout:', 'countdown-time')}</Title>
                            <SelectControl value={layout} onChange={val => {
                                setAttributes({ layout: val });
                                'default' === val && setAttributes({
                                    boxIsInline: false,
                                    background: { type: 'solid', color: '#0000', styles: '' },
                                    shadow: { hOffset: '0px', vOffset: '0px', color: '#0000', styles: 'none' },
                                    boxBG: { type: 'gradient', color: '#4527a4', gradient: 'linear-gradient(135deg, #4527a4, #8344c5)', styles: 'background-image: linear-gradient(135deg, #4527a4, #8344c5);' },
                                    boxSpace: '30px',
                                    boxBorder: { width: '0px', type: 'solid', color: '#0000', radius: '5%', styles: 'border-radius: 5%;' },
                                    digitColor: '#fff',
                                    labelColor: '#fff',
                                    isSep: false,
                                    sepColor: '#4527a4'
                                });
                                'layout1' === val && setAttributes({
                                    boxIsInline: false,
                                    background: { type: 'gradient', gradient: 'linear-gradient(180deg, #4527a4, #8344c5)', styles: 'background-image: linear-gradient(180deg, #4527a4, #8344c5);' },
                                    shadow: { hOffset: '5px', vOffset: '5px', color: '#4527a4', styles: '5px 5px 0px 0px #4527a4' },
                                    boxBG: { type: 'solid', color: '#0000', styles: 'background-color: #0000;' },
                                    boxSpace: '0px',
                                    boxBorder: { width: '0px', type: 'solid', color: '#0000', radius: '5%', styles: 'border-radius: 5%;' },
                                    digitColor: '#fff',
                                    labelColor: '#fff',
                                    isSep: true,
                                    sepColor: '#fff'
                                });
                                'layout2' === val && setAttributes({
                                    boxIsInline: false,
                                    background: { type: 'solid', color: '#0000', styles: '' },
                                    shadow: { hOffset: '0px', vOffset: '0px', color: '#0000', styles: 'none' },
                                    boxBG: { type: 'solid', color: '#0000', styles: 'background-color: #0000;' },
                                    boxSpace: '30px',
                                    boxBorder: { width: '2px', type: 'solid', color: '#4527a4', radius: '5%', styles: 'border-top: 2px solid #4527a4; border-right: 2px solid #4527a4; border-bottom: 2px solid #4527a4; border-left: 2px solid #4527a4; border-radius: 5%;' },
                                    digitColor: '#4527a4',
                                    labelColor: '#4527a4',
                                    isSep: true,
                                    sepColor: '#4527a4'
                                });
                                'layout3' === val && setAttributes({
                                    boxIsInline: true,
                                    background: { type: 'solid', color: '#0000', styles: '' },
                                    shadow: { hOffset: '0px', vOffset: '8px', blur: '15px', color: '#4527a4', styles: '0px 8px 15px 0px #4527a4' },
                                    boxBG: { type: 'solid', color: '#0000', styles: 'background-color: #0000;' },
                                    boxSpace: '30px',
                                    boxBorder: { width: '0px', type: 'solid', color: '#0000', radius: '5%', styles: 'border-radius: 5%;' },
                                    digitColor: '#4527a4',
                                    labelColor: '#4527a4',
                                    isSep: true,
                                    sepColor: '#4527a4'
                                });
                            }} options={layouts} />
                        </PanelRow>

                        {/* Box */}
                        <ToggleControl className='mt20' label={__('Box Inline?', 'countdown-time')} checked={boxIsInline} onChange={val => setAttributes({ boxIsInline: val })} />

                        {/* Labels */}
                        <ToggleControl label={__('Show Labels', 'countdown-time')} checked={isLabels} onChange={val => setAttributes({ isLabels: val })} />

                        {/* Days */}
                        <ToggleControl label={__('Show Days', 'countdown-time')} checked={isDays} onChange={val => setAttributes({ isDays: val })} />

                        {/* Hours */}
                        <ToggleControl label={__('Show Hours', 'countdown-time')} checked={isHours} onChange={val => setAttributes({ isHours: val })} />

                        {/* Minutes */}
                        <ToggleControl label={__('Show Minutes', 'countdown-time')} checked={isMinutes} onChange={val => setAttributes({ isMinutes: val })} />

                        {/* Seconds */}
                        <ToggleControl label={__('Show Seconds', 'countdown-time')} checked={isSeconds} onChange={val => setAttributes({ isSeconds: val })} />
                    </PanelBody>
                </>}

                {'style' == tab.name && <>
                    <PanelBody className='bPlPanelBody' title={__('Countdown Style', 'countdown-time')}>
                        <UnitControl label={__('Width:', 'countdown-time')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit, perUnit, emUnit]} />
                        <small>{__('Keep width 0, to auto width.', 'countdown-time')}</small>

                        <Background label={__('Background:', 'countdown-time')} background={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#0000' }} />

                        <SpaceControl className='mt20' label={__('Padding:', 'countdown-time')} space={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '10px', horizontal: '15px' }} />

                        <ShadowControl label={__('Shadow:', 'countdown-time')} shadow={shadow} onChange={val => setAttributes({ shadow: val })} />
                    </PanelBody>

                    <PanelBody className='bPlPanelBody' title={__('Boxes Style', 'countdown-time')} initialOpen={false}>
                        {'0px' === width || '0%' === width || '0em' === width ? '' : <PanelRow>
                            <Title mt='0' mb='0'>{__('Box Position:', 'countdown-time')}</Title>
                            <SelectControl value={boxPosition} onChange={val => setAttributes({ boxPosition: val })} options={flexPos} />
                        </PanelRow>}

                        {/* Background */}
                        <Background label={__('Background:', 'countdown-time')} background={boxBG} onChange={val => setAttributes({ boxBG: val })} defaults={{ type: 'gradient', color: '#4527a4', gradient: 'linear-gradient(135deg, #4527a4, #8344c5)' }} />

                        {/* Width */}
                        <UnitControl className='mt20' label={__('Width:', 'countdown-time')} labelPosition='left' value={boxWidth} onChange={val => setAttributes({ boxWidth: val })} units={[pxUnit, emUnit, remUnit]} />

                        {/* Height */}
                        <UnitControl className='mt20' label={__('Height:', 'countdown-time')} labelPosition='left' value={boxHeight} onChange={val => setAttributes({ boxHeight: val })} units={[pxUnit, emUnit, remUnit]} />

                        {/* Space Between Boxes */}
                        <UnitControl className='mt20' label={__('Space Between Boxes:', 'countdown-time')} labelPosition='left' value={boxSpace} onChange={val => setAttributes({ boxSpace: val })} units={[pxUnit, emUnit, remUnit]} />

                        <BorderControl label={__('Border', 'countdown-time')} border={boxBorder} onChange={val => setAttributes({ boxBorder: val })} defaults={{ radius: '5%' }} />

                        <ShadowControl label={__('Shadow:', 'countdown-time')} shadow={boxShadow} onChange={val => setAttributes({ boxShadow: val })} />
                    </PanelBody>


                    {isDays || isHours || isMinutes || isSeconds ? <PanelBody className='bPlPanelBody' title={__('Content Style', 'countdown-time')} initialOpen={false}>
                        {/* Digit Typography */}
                        <Typography label={__('Digit Typography:', 'countdown-time')} typography={digitTypo} onChange={val => setAttributes({ digitTypo: val })} defaults={{ fontSize: 48 }} />

                        {/* Digit Color */}
                        <BColor label={__('Digit Color:', 'countdown-time')} value={digitColor} onChange={val => setAttributes({ digitColor: val })} defaultColor='#fff' />

                        {isLabels && <>
                            {/* Label Typography */}
                            <Typography className='mt20' label={__('Label Typography:', 'countdown-time')} typography={labelTypo} onChange={val => setAttributes({ labelTypo: val })} defaults={{ fontSize: 18 }} />

                            {/* Label Color */}
                            <BColor label={__('Label Color:', 'countdown-time')} value={labelColor} onChange={val => setAttributes({ labelColor: val })} defaultColor='#fff' />
                        </>}
                    </PanelBody> : null}


                    <PanelBody className='bPlPanelBody' title={__('Separator', 'countdown-time')} initialOpen={false}>
                        <ToggleControl label={__('Show Separator', 'countdown-time')} checked={isSep} onChange={val => setAttributes({ isSep: val })} />

                        {isSep && <>
                            <PanelRow>
                                <Title mt='0' mb='0'>{__('Type:', 'countdown-time')}</Title>
                                <SelectControl value={sepType} onChange={val => setAttributes({ sepType: val })} options={sepTypes} />
                            </PanelRow>

                            <Title>{__('Size:', 'countdown-time')}</Title>
                            <RangeControl value={sepSize} onChange={val => setAttributes({ sepSize: val })} min={0} max={160} step={1} allowReset={true} resetFallbackValue={48} initialPosition={48} />

                            <BColor label={__('Color:', 'countdown-time')} value={sepColor} onChange={val => setAttributes({ sepColor: val })} defaultColor='#4527a4' />
                        </>}
                    </PanelBody>
                </>}
            </>}</TabPanel>
        </InspectorControls>

        <BlockControls>
            <AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('Countdown Time Alignment')} alignmentControls={[
                { title: __('Countdown Time in left', 'countdown-time'), align: 'flex-start', icon: 'align-left' },
                { title: __('Countdown Time in center', 'countdown-time'), align: 'center', icon: 'align-center' },
                { title: __('Countdown Time in right', 'countdown-time'), align: 'flex-end', icon: 'align-right' }
            ]} />
        </BlockControls>
    </>;
};
export default Settings;